import { useState, useEffect } from 'react';
import { Project } from '@/types/project';
import { motion, AnimatePresence } from 'framer-motion';

interface ProjectCardProps {
  project: Project;
  onDecline: () => void;
  onSave: () => void;
  onApply: () => void;
  swipeDirection: 'left' | 'right' | 'up' | null;
}

const ProjectCard = ({ project, onDecline, onSave, onApply, swipeDirection }: ProjectCardProps) => {
  const [isAnimating, setIsAnimating] = useState(false);
  const [animateDirection, setAnimateDirection] = useState<{ x: number, y: number }>({ x: 0, y: 0 });
  
  // Reset animation when project changes
  useEffect(() => {
    setAnimateDirection({ x: 0, y: 0 });
    setIsAnimating(false);
  }, [project]);

  // Handle swipe animation
  useEffect(() => {
    if (swipeDirection) {
      setIsAnimating(true);
      
      if (swipeDirection === 'left') {
        setAnimateDirection({ x: -1000, y: 0 });
      } else if (swipeDirection === 'right') {
        setAnimateDirection({ x: 1000, y: 0 });
      } else if (swipeDirection === 'up') {
        setAnimateDirection({ x: 0, y: -1000 });
      }
    } else {
      setAnimateDirection({ x: 0, y: 0 });
      
      // Add a small delay before allowing new interactions
      const resetTimer = setTimeout(() => {
        setIsAnimating(false);
      }, 50);
      
      return () => clearTimeout(resetTimer);
    }
  }, [swipeDirection]);

  return (
    <div className="relative w-full max-w-md">
      {/* Project card */}
      <AnimatePresence mode="wait">
        <motion.div
          key={`card-${project.id}`}
          className="bg-white rounded-xl shadow-lg overflow-hidden"
          initial={{ scale: 0.95, opacity: 0.5, x: 0, y: 0 }}
          animate={{
            x: animateDirection.x,
            y: animateDirection.y,
            scale: 1,
            opacity: 1,
            transition: { 
              type: 'spring', 
              stiffness: 500, 
              damping: 30,
              opacity: { duration: 0.2 }
            }
          }}
          exit={{
            x: animateDirection.x,
            y: animateDirection.y,
            opacity: 0,
            scale: 0.8,
            transition: { duration: 0.2 }
          }}
          style={{
            boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
          }}
        >
          <div className="p-6">
            <h2 className="text-xl font-bold text-gray-800">{project.title}</h2>
            <p className="text-sm text-gray-500 mt-1">
              {project.faculty} • {project.department}
            </p>
            
            <div className="mt-4">
              <p className="text-gray-700">{project.description}</p>
            </div>
            
            <div className="mt-4">
              <h3 className="text-sm font-semibold text-gray-700">Skills Required:</h3>
              <div className="mt-2 flex flex-wrap gap-2">
                {project.skills.map((skill: string, index: number) => (
                  <span 
                    key={index} 
                    className="px-2 py-1 bg-purple-50 text-purple-700 text-xs rounded-full"
                  >
                    {skill}
                  </span>
                ))}
              </div>
            </div>
            
            <div className="mt-4 grid grid-cols-2 gap-4">
              <div>
                <h3 className="text-sm font-semibold text-gray-700">Duration:</h3>
                <p className="text-sm text-gray-600">{project.duration}</p>
              </div>
              <div>
                <h3 className="text-sm font-semibold text-gray-700">Commitment:</h3>
                <p className="text-sm text-gray-600">{project.commitment}</p>
              </div>
            </div>
          </div>
        </motion.div>
      </AnimatePresence>

      {/* Action buttons */}
      <div className="mt-6 flex justify-between items-center">
        <button 
          onClick={onDecline}
          disabled={isAnimating}
          className="w-16 h-16 flex items-center justify-center rounded-full bg-white shadow-md text-red-500 hover:bg-red-50 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          aria-label="Decline"
        >
          <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
        
        <button 
          onClick={onSave}
          disabled={isAnimating}
          className="w-16 h-16 flex items-center justify-center rounded-full bg-white shadow-md text-violet-500 hover:bg-violet-50 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          aria-label="Save"
        >
          <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 5a2 2 0 012-2h10a2 2 0 012 2v16l-7-3.5L5 21V5z" />
          </svg>
        </button>
        
        <button 
          onClick={onApply}
          disabled={isAnimating}
          className="w-16 h-16 flex items-center justify-center rounded-full bg-white shadow-md text-violet-700 hover:bg-violet-50 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          aria-label="Apply"
        >
          <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
          </svg>
        </button>
      </div>
    </div>
  );
};

export default ProjectCard; 