import { useState, useEffect } from 'react';
import { Project } from '@/types/project';
import ProjectCard from './ProjectCard';

interface DiscoverTabProps {
  projects: Project[];
  onSaveProject: (project: Project) => void;
  onApplyProject: (project: Project) => void;
}

const DiscoverTab = ({ projects, onSaveProject, onApplyProject }: DiscoverTabProps) => {
  const [currentProjectIndex, setCurrentProjectIndex] = useState(0);
  const [swipeDirection, setSwipeDirection] = useState<'left' | 'right' | 'up' | null>(null);
  const [isTransitioning, setIsTransitioning] = useState(false);
  const currentProject = projects[currentProjectIndex];

  // Handle animation end
  useEffect(() => {
    if (swipeDirection) {
      setIsTransitioning(true);
      
      // First timeout for the animation to complete
      const animationTimer = setTimeout(() => {
        // Process the action based on direction
        if (swipeDirection === 'up') {
          onSaveProject(currentProject);
        } else if (swipeDirection === 'right') {
          onApplyProject(currentProject);
        }
        
        // Reset direction to null first to trigger card reset
        setSwipeDirection(null);
        
        // Second timeout to move to the next card after animation completes
        const transitionTimer = setTimeout(() => {
          // Move to next project if available
          if (currentProjectIndex < projects.length - 1) {
            setCurrentProjectIndex(prev => prev + 1);
          } else {
            // Reset to first project when we reach the end
            setCurrentProjectIndex(0);
          }
          
          // End the transition state
          setIsTransitioning(false);
        }, 300); // Short delay to ensure smooth transition
        
        return () => clearTimeout(transitionTimer);
      }, 400); // Duration for the animation
      
      return () => clearTimeout(animationTimer);
    }
  }, [swipeDirection, currentProject, currentProjectIndex, projects.length, onSaveProject, onApplyProject]);

  // Prevent actions while transitioning
  const handleDecline = () => {
    if (isTransitioning) return;
    setSwipeDirection('left');
  };

  const handleSave = () => {
    if (isTransitioning) return;
    setSwipeDirection('up');
  };

  const handleApply = () => {
    if (isTransitioning) return;
    setSwipeDirection('right');
  };

  // Handle empty projects case
  if (projects.length === 0) {
    return (
      <div className="w-full mt-6 flex flex-col items-center">
        <div className="text-center py-12">
          <p className="text-xl text-gray-600">No projects to show.</p>
          <p className="text-gray-500 mt-2">Check back later for new opportunities!</p>
        </div>
      </div>
    );
  }

  return (
    <div className="w-full mt-6 flex flex-col items-center">
      <div className="relative w-full max-w-md" style={{ minHeight: '400px' }}>
        <ProjectCard
          key={`project-${currentProject.id}-${currentProjectIndex}`}
          project={currentProject}
          onDecline={handleDecline}
          onSave={handleSave}
          onApply={handleApply}
          swipeDirection={swipeDirection}
        />
      </div>
      
      <div className="mt-6 text-center text-gray-500">
        <p>Use the buttons below to interact with research opportunities</p>
        <p className="mt-1 text-sm">
          <span className="text-red-500 font-medium">✕</span> to decline, 
          <span className="text-violet-500 font-medium ml-1">🔖</span> to save for later, or 
          <span className="text-violet-700 font-medium ml-1">✓</span> to apply
        </p>
      </div>
    </div>
  );
};

export default DiscoverTab; 